<template>
    <div>
      <h1>Login Form</h1>
      <input type="text" placeholder="username" name="username" v-model="email">
      <br>
      <input type="password" placeholder="password" name="password" v-model="pass">
      <br>
      <button type="submit" name="buttonLogin" @click="login()">Login</button>
    </div>
    </template>
    
    <script>
    import authenticationService from '../servicios/AuthenticationServices'
    export default {
      data(){
        return {
          email: '',
          pass: ''
        }
      },
      methods:{
          async login(){
            const consulta = await authenticationService.login({
              "email": this.email,
              "pass": this.pass
            })
            this.$store.commit('setUserInfo',consulta.data)
          }
        }
      }
    
    </script>
    <style>
    
    </style>
    